/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .site-header {
    background-position: top;
  }
}

.header-grid {
  padding: 5rem 0;
}

.logo {
  margin: auto;
  width: 20rem;
}
@media (min-width: 768px) {
  .logo {
    margin: 0;
  }
}

.contenido-header {
  color: #FFFFFF;
  padding: 10rem 0;
}
.contenido-header a {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.contenido-header a:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .contenido-header a {
    width: auto;
    display: inline-block;
  }
}
.contenido-header h1 {
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .contenido-header {
    padding: 15rem 0;
    width: 50%;
  }
  .contenido-header h1 {
    font-size: 6rem;
  }
}

@media (min-width: 768px) {
  .crea-contenido {
    display: flex;
    align-items: center;
    padding: 5rem 0;
    gap: 4rem;
  }
}

.crea-imagen {
  width: 100%;
  margin: 0 auto;
  padding: 0 0 4rem 0;
}

.crea-texto h2 {
  font-size: 4rem;
  display: inline-block;
  color: #313c4e;
}
.crea-texto h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% - 1rem);
  background-color: #84d9bc;
}

.negocios {
  background-color: #84d9bc;
}

.negocios-contenido {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .negocios-contenido {
    display: flex;
    align-items: center;
    gap: 5rem;
  }
}

.negocios-texto h2 {
  font-size: 4rem;
  display: inline-block;
  color: #313c4e;
}
.negocios-texto h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% - 1rem);
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .negocios-texto {
    width: 70%;
  }
}

.negocios-imagen img {
  width: 80%;
  margin: 0 auto;
}
.negocios-imagen img:hover {
  transform: scale(1.1) rotate(3deg);
  transition: all ease-in 300ms;
}

.estadisticas {
  padding: 5rem 0;
}

.estadisticas-texto h2 {
  font-size: 4rem;
  display: inline-block;
  color: #313c4e;
}
.estadisticas-texto h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% - 1rem);
  background-color: #84d9bc;
}

.precios {
  background-color: #313c4e;
  padding: 5rem 0;
}
.precios h2 {
  font-size: 4rem;
  display: inline-block;
  color: #FFFFFF;
}
.precios h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% - 1rem);
  background-color: #84d9bc;
}
.precios .descripcion {
  color: #FFFFFF;
}

.planes {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .planes {
    flex-direction: row;
    padding: 5rem 0;
  }
}

.plan {
  background-color: #FFFFFF;
  width: 100%;
  padding: 4rem 0;
  text-align: center;
  border-bottom: 1px solid #e1e1e1;
  padding: 16rem 4rem 4rem;
}
.plan .precio {
  font-weight: 900;
}
.plan .listado li {
  margin: 2rem 0;
}

.negocio {
  order: -1;
  background-color: #84d9bc;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .negocio {
    order: unset;
    border-radius: 2rem;
    margin: -5rem 0;
  }
}
.negocio button {
  cursor: pointer;
  padding: 1rem 2rem;
  width: 100%;
  background-color: transparent;
  border: 2px solid #FFFFFF;
  font-size: 2rem;
  color: #313c4e;
  font-weight: 900;
}
@media (min-width: 768px) {
  .negocio button {
    width: auto;
  }
}
.negocio button:hover {
  background-color: #5dcda7;
  transition: background-color ease-in 300ms;
}

.inicio {
  background-color: #FFFFFF;
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.inicio button {
  cursor: pointer;
  padding: 1rem 2rem;
  width: 100%;
  margin: 0 auto;
  background-color: #84d9bc;
  font-size: 2rem;
  color: #313c4e;
  font-weight: 900;
  border: 0;
}
@media (min-width: 768px) {
  .inicio button {
    width: auto;
  }
}
.inicio button:hover {
  background-color: #5dcda7;
  transition: background-color ease-in 300ms;
}

.pro {
  background-color: #FFFFFF;
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.pro button {
  cursor: pointer;
  padding: 1rem 2rem;
  width: 100%;
  background-color: #84d9bc;
  font-size: 2rem;
  color: #313c4e;
  font-weight: 900;
  border: 0;
}
@media (min-width: 768px) {
  .pro button {
    width: auto;
  }
}
.pro button:hover {
  background-color: #5dcda7;
  transition: background-color ease-in 300ms;
}

.testimonios {
  padding: 5rem 0;
}
.testimonios h2 {
  font-size: 4rem;
  display: inline-block;
  color: #313c4e;
}
.testimonios h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% - 1rem);
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .testimonios {
    display: flex;
    align-items: center;
    gap: 5rem;
  }
}

.testimonios-imagen {
  margin-bottom: 4rem;
}
.testimonios-imagen img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimonios-imagen img {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .testimonios-contenido {
    max-width: 60%;
  }
  .testimonios-contenido .testimonio:nth-child(2) {
    margin-left: 5rem;
  }
  .testimonios-contenido .testimonio:nth-child(3) {
    margin-left: 10rem;
  }
  .testimonios-contenido .testimonio:nth-child(4) {
    margin-left: 15rem;
  }
}

.testimonio {
  border: 5px solid #313c4e;
  padding: 2rem;
  margin-bottom: 5rem;
  border-radius: 2rem;
  background-color: #84d9bc;
  position: relative;
}
.testimonio p {
  margin-left: 5rem;
}
.testimonio p::before {
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  content: "";
  width: 5rem;
  height: 5rem;
  display: inline-block;
  position: absolute;
  left: 1rem;
}

.footer {
  background-color: #313c4e;
}

.footer-logo {
  margin: 0 auto;
  width: 50%;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-logo {
    margin: 0;
    width: 20%;
  }
}

.footer-contenido {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer-contenido {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .widgets {
    display: flex;
    gap: 5rem;
  }
}

.widget {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.widget a {
  margin-bottom: 1rem;
  color: #FFFFFF;
}
.widget h3 {
  color: #84d9bc;
}

.copyright {
  font-size: 1.2rem;
  color: #FFFFFF;
  margin: 0;
  border-top: 2px solid #84d9bc;
  text-align: center;
  padding: 1rem 0;
}